@import url(https://fonts.googleapis.com/icon?family=Material+Icons|Material+Icons+Outlined);
html {
  font-size: 16px;
}

@media only screen and (min-width: 730px) and (min-height: 1000px) {
  html {
    font-size: 28px;
  }
}

@media only screen and (min-width: 1080px) and (min-height: 1920px) {
  html {
    font-size: 40px;
  }
}

body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  overflow-x: hidden;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

* {
  margin: 0;
}

.eventContainer {
  height: 100%;
}
.animationContainer {
  position: absolute;
  top: 0;
  left: 0;
  width: 0;
  height: 0;
  overflow: hidden;
}
.animationContainer.animated {
  width: 100vw;
  height: 100vh;
}
.circle {
  position: absolute;
  border-radius: 50%;
  box-sizing: border-box;
}
.scale-anim {
  width: max(100vw, 100vh);
  height: max(100vw, 100vh);
  -webkit-animation: scale 1s ease-in forwards alternate 2;
          animation: scale 1s ease-in forwards alternate 2;
  z-index: 10;
}
.scale-hole-anim {
  width: max(100vw, 100vh);
  height: max(100vw, 100vh);
  -webkit-animation: scale 1.5s ease-in forwards, hole 1s ease-in 1.5s forwards;
          animation: scale 1.5s ease-in forwards, hole 1s ease-in 1.5s forwards;
  z-index: 10;
}
@-webkit-keyframes scale {
  0% {
    -webkit-transform: scale(0);
            transform: scale(0);
    background-color: #fff;
  }
  100% {
    -webkit-transform: scale(2.4);
            transform: scale(2.4);
    background-color: #42b5f9;
  }
}
@keyframes scale {
  0% {
    -webkit-transform: scale(0);
            transform: scale(0);
    background-color: #fff;
  }
  100% {
    -webkit-transform: scale(2.4);
            transform: scale(2.4);
    background-color: #42b5f9;
  }
}
@-webkit-keyframes hole {
  0% {
    border: solid max(50vw, 50vh);
    border-color: #42b5f9;
    background-color: transparent;
  }
  100% {
    border: solid 0;
    border-color: #fff;
    background-color: transparent;
  }
}
@keyframes hole {
  0% {
    border: solid max(50vw, 50vh);
    border-color: #42b5f9;
    background-color: transparent;
  }
  100% {
    border: solid 0;
    border-color: #fff;
    background-color: transparent;
  }
}

.home {
  height: 100%;
  padding-right: 0.25rem;
  color: gray;
}
.home__loading {
  position: absolute;
  top: 50%;
  left: 50%;
  margin: -1.5rem 0 0 -1.5rem;
  fill: #c7c7c7;
}

.home header {
  margin-top: 5vh;
}

.home main {
  width: 100vw;
  position: absolute;
  top: 18vh;
  display: flex;
  flex-direction: column;
  align-items: center;
}

.home main .logo {
  width: 15.625rem;
}

.home main h5 {
  text-align: center;
  margin: 0.312rem 0;
}

.home main span {
  color: #0095f6;
  cursor: pointer;
  -webkit-tap-highlight-color: transparent;
}

.home .loginguest {
  color: rgba(0, 0, 0, 0.2);
  cursor: pointer;
}

.home footer {
  width: 100vw;
  text-align: center;
  position: absolute;
  bottom: 4.375rem;
}

.home footer h3 {
  color: #555555;
}

@media screen and (max-height: 450px) {
  .home .lottie_player {
    display: none;
  }
}

@media screen and (max-height: 300px) {
  .home footer {
    display: none;
  }
}

.signform {
  display: flex;
  flex-direction: column;
}

.signform__header_img {
  width: 8rem;
  margin: 1rem 0 2rem;
}

.main {
  background-color: #fafafa;
}

.main header {
  position: sticky;
  top: 0;
  z-index: 2;
  background-color: white;
  padding: 0.312rem 0.625rem;
  border-bottom: 0.062rem solid lightgray;
  object-fit: contain;
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.main header .new_story,
.main header .avatar,
.about__headerback {
  font-size: 0;
  padding: 0.5rem;
  background: 0 0;
  border: 0;
  cursor: pointer;
}

.main header .logo {
  object-fit: contain;
  width: 5rem;
}

.main article.hidden {
  opacity: 0;
  height: 80vh;
  overflow: hidden;
}

.main article {
  padding: 1.25rem;
  display: flex;
  justify-content: center;
  transition: opacity 1s;
}

.imageuploader {
  display: flex;
  flex-direction: column;
  margin: 0.625rem auto;
}

.imageuploader__progress {
  width: 100%;
}

.imageuploader__caption {
  border-width: 0.062rem;
  margin-top: 0.25rem;
  margin-bottom: 0.25rem;
  font: 400 0.833rem Arial;
}

.imageuploader__title {
  margin-bottom: 1rem;
}

.imageuploader__previewContainer {
  min-height: 6.25rem;
  max-height: 20rem;
  overflow: hidden;
  border: 0.062rem solid lightgray;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  color: gray;
}

.imageuploader__preview {
  max-width: 18.75rem;
  width: 100%;
  height: auto;
}

.imageuploader__loader {
  font-size: 0.833rem;
  margin-top: 0.625rem;
}

.post {
  background-color: white;
  max-width: 31.25rem;
  border: 1px solid lightgray;
  border-radius: 0.312rem;
  margin-bottom: 2.812rem;
}

.post__header {
  display: flex;
  align-items: center;
  padding: 1.25rem;
}

.post__image {
  width: 100%;
  object-fit: contain;
  border-top: 0.062rem solid lightgray;
  border-bottom: 0.062rem solid lightgray;
}

.post__text {
  font-weight: normal;
}

.post__body {
  padding: 0 1rem;
}

section {
  display: flex;
  flex-shrink: 0;
  align-items: stretch;
  margin-top: 0.25rem;
}

.post__buttonIcon {
  font-size: 0;
  padding: 0.5rem;
  background: 0 0;
  border: 0;
  cursor: pointer;
}

.iconlike {
  margin-left: -0.5rem;
}

.iconsave {
  display: inline-block;
  margin-left: auto;
  margin-right: -0.625rem;
}

.post__likeCount {
  margin-bottom: 0.625rem;
}

.post__comments {
  margin-top: 1.875rem;
  margin-bottom: 1.25rem;
}

.post__comments p {
  margin-top: 0.312rem;
  margin-bottom: 0.625rem;
}

.post__commentBox {
  border-top: 0.062rem solid lightgray;
  display: flex;
}

.post__input {
  flex: 1 1;
  border: none;
  padding: 0.625rem;
}

.post__button {
  flex: 0 1;
  border: none;
  color: #6082a3;
  background-color: transparent;
  margin: 0 0.25rem;
}

.loading-area {
  position: fixed;
  top: 0;
  left: 0;
  width: 100vw;
  height: 100vh;
  overflow: hidden;
  background-color: white;
  z-index: 1;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

